	module.exports = {"data":{"markdownRemark":{"html":"<p>In order to use the HackerOne VPN, you need to install a VPN client that supports OpenVPN.</p>\n<p>Refer to these installation and configuration instructions for your platform:</p>\n<ul>\n<li><a href=\"#windows\">Windows</a></li>\n<li><a href=\"#macos\">macOS</a></li>\n</ul>\n<h3 id=\"windows\">Windows</h3>\n<p>To configure the OpenVPN client to your windows machine:</p>\n<ol>\n<li>\n<p>Download the OpenVPN <strong>Windows Installer</strong> from <a href=\"https://openvpn.net/community-downloads/\" target=\"_blank\">this page</a>.</p>\n</li>\n<li>\n<p>Execute the OpenVPN Installer.</p>\n</li>\n<li>\n<p>Select these components when prompted:</p>\n<ul>\n<li>OpenVPN Service</li>\n<li>Tap Virtual Ethernet Adapter</li>\n<li>OpenVPN GUI</li>\n</ul>\n</li>\n</ol>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/vpn-windows-gui-1-52ac20669d896bcb4761d46888fc3084-eb784.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 77.73359840954274%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"Screenshot\"\n        title=\"\"\n        src=\"/static/vpn-windows-gui-1-52ac20669d896bcb4761d46888fc3084-2f183.png\"\n        srcset=\"/static/vpn-windows-gui-1-52ac20669d896bcb4761d46888fc3084-67829.png 125w,\n/static/vpn-windows-gui-1-52ac20669d896bcb4761d46888fc3084-5c59f.png 250w,\n/static/vpn-windows-gui-1-52ac20669d896bcb4761d46888fc3084-2f183.png 500w,\n/static/vpn-windows-gui-1-52ac20669d896bcb4761d46888fc3084-c93e8.png 750w,\n/static/vpn-windows-gui-1-52ac20669d896bcb4761d46888fc3084-4e628.png 1000w,\n/static/vpn-windows-gui-1-52ac20669d896bcb4761d46888fc3084-eb784.png 1006w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<ol start=\"4\">\n<li>Click <strong>Install</strong> when prompted to install a network adapter.</li>\n</ol>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/vpn-windows-gui-2-42a50f7f961e7058c0e67705e099b4dc-2d333.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 45.4054054054054%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"Screenshot\"\n        title=\"\"\n        src=\"/static/vpn-windows-gui-2-42a50f7f961e7058c0e67705e099b4dc-2f183.png\"\n        srcset=\"/static/vpn-windows-gui-2-42a50f7f961e7058c0e67705e099b4dc-67829.png 125w,\n/static/vpn-windows-gui-2-42a50f7f961e7058c0e67705e099b4dc-5c59f.png 250w,\n/static/vpn-windows-gui-2-42a50f7f961e7058c0e67705e099b4dc-2f183.png 500w,\n/static/vpn-windows-gui-2-42a50f7f961e7058c0e67705e099b4dc-c93e8.png 750w,\n/static/vpn-windows-gui-2-42a50f7f961e7058c0e67705e099b4dc-4e628.png 1000w,\n/static/vpn-windows-gui-2-42a50f7f961e7058c0e67705e099b4dc-2d333.png 1110w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<ol start=\"5\">\n<li>Click <b>Finish</b> to complete the setup.</li>\n</ol>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/vpn-windows-gui-3-3ccd6891a3a45e62b8ab71bb7dd85b86-727d9.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 77.57936507936508%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"Screenshot\"\n        title=\"\"\n        src=\"/static/vpn-windows-gui-3-3ccd6891a3a45e62b8ab71bb7dd85b86-2f183.png\"\n        srcset=\"/static/vpn-windows-gui-3-3ccd6891a3a45e62b8ab71bb7dd85b86-67829.png 125w,\n/static/vpn-windows-gui-3-3ccd6891a3a45e62b8ab71bb7dd85b86-5c59f.png 250w,\n/static/vpn-windows-gui-3-3ccd6891a3a45e62b8ab71bb7dd85b86-2f183.png 500w,\n/static/vpn-windows-gui-3-3ccd6891a3a45e62b8ab71bb7dd85b86-c93e8.png 750w,\n/static/vpn-windows-gui-3-3ccd6891a3a45e62b8ab71bb7dd85b86-4e628.png 1000w,\n/static/vpn-windows-gui-3-3ccd6891a3a45e62b8ab71bb7dd85b86-727d9.png 1008w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<ol start=\"7\">\n<li>\n<p>Start the OpenVPN GUI application.</p>\n</li>\n<li>\n<p>Right click the OpenVPN icon in the bottom right corner of the screen, and click <b>Import file...</b>.</p>\n</li>\n</ol>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/vpn-windows-gui-4-992129738553397ca13f380e6b776413-dc9d4.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 37.81321184510251%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"Screenshot\"\n        title=\"\"\n        src=\"/static/vpn-windows-gui-4-992129738553397ca13f380e6b776413-2f183.png\"\n        srcset=\"/static/vpn-windows-gui-4-992129738553397ca13f380e6b776413-67829.png 125w,\n/static/vpn-windows-gui-4-992129738553397ca13f380e6b776413-5c59f.png 250w,\n/static/vpn-windows-gui-4-992129738553397ca13f380e6b776413-2f183.png 500w,\n/static/vpn-windows-gui-4-992129738553397ca13f380e6b776413-c93e8.png 750w,\n/static/vpn-windows-gui-4-992129738553397ca13f380e6b776413-dc9d4.png 878w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<ol start=\"9\">\n<li>Select the <code>*.openvpn</code> file you downloaded from HackerOne (See <a href=\"/hackers/configure-the-hackerone-vpn.html\">Accessing your VPN Credentials</a>).</li>\n</ol>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/vpn-windows-gui-5-6cb73646351ffac2c889e2acfdde4def-7ed25.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 67.96338672768879%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"Screenshot\"\n        title=\"\"\n        src=\"/static/vpn-windows-gui-5-6cb73646351ffac2c889e2acfdde4def-2f183.png\"\n        srcset=\"/static/vpn-windows-gui-5-6cb73646351ffac2c889e2acfdde4def-67829.png 125w,\n/static/vpn-windows-gui-5-6cb73646351ffac2c889e2acfdde4def-5c59f.png 250w,\n/static/vpn-windows-gui-5-6cb73646351ffac2c889e2acfdde4def-2f183.png 500w,\n/static/vpn-windows-gui-5-6cb73646351ffac2c889e2acfdde4def-c93e8.png 750w,\n/static/vpn-windows-gui-5-6cb73646351ffac2c889e2acfdde4def-7ed25.png 874w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<ol start=\"10\">\n<li>Right click the OpenVPN icon on the bottom right corner of the screen, and click <b>Connect</b>.</li>\n</ol>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/vpn-windows-gui-6-721ad61a24746e6e36557871256e7fe6-53fa9.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 57.25806451612904%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"Screenshot\"\n        title=\"\"\n        src=\"/static/vpn-windows-gui-6-721ad61a24746e6e36557871256e7fe6-2f183.png\"\n        srcset=\"/static/vpn-windows-gui-6-721ad61a24746e6e36557871256e7fe6-67829.png 125w,\n/static/vpn-windows-gui-6-721ad61a24746e6e36557871256e7fe6-5c59f.png 250w,\n/static/vpn-windows-gui-6-721ad61a24746e6e36557871256e7fe6-2f183.png 500w,\n/static/vpn-windows-gui-6-721ad61a24746e6e36557871256e7fe6-c93e8.png 750w,\n/static/vpn-windows-gui-6-721ad61a24746e6e36557871256e7fe6-53fa9.png 992w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>The VPN will start connecting.</p>\n<h3 id=\"macos\">macOS</h3>\n<p>To configure the OpenVPN client on macOS:</p>\n<ol>\n<li>Install <a href=\"https://tunnelblick.net/cInstall.html\">Tunnelblick</a>.</li>\n<li>Start Tunnelblick.</li>\n<li>Find the <code>*.openvpn</code> file you have downloaded from HackerOne (See: <a href=\"/hackers/configure-the-hackerone-vpn.html\">Accessing your VPN Credentials</a>).</li>\n<li>Drag it onto the Tunnelblick icon at the top of your screen.</li>\n<li>Click the Tunnelblick icon, and click the HackerOne VPN connection.</li>\n</ol>\n<p>Your VPN will now be connected.</p>","frontmatter":{"title":"Configuring OpenVPN Clients","id":"hackers/openvpn-clients","path":"/hackers/openvpn-clients.html","description":null}}},"pathContext":{}}

